<template>
  <div>
    <div id="header">
      <header-nav></header-nav>
    </div>
    <div class="content mt30 clearfix padding-top-60" id="content">
      <!--左边区域-->
      <div id="left" class="mr30 mt30">
        <!--封面图-->
        <div class="bg-img mainImg br5"
             :style="{backgroundImage:'url('+ osshost + themeDetail.topicimg + detailpic +')'}"></div>
        <div class="menu_info mt15">
          <div class="name">{{themeDetail.title}}</div>
          <div class="info">
            <div class="mainColor">{{themeDetail.menuClass}}</div>
            <div class="grayColor ">
              <span><i class="iconfont">&#xe66d;</i>{{themeDetail.collections}}</span>
              <span><i class="iconfont"> &#xe65d;</i>{{themeDetail.visits}}</span>
            </div>
            <div @click="collectTheme()" class="btn-sc" v-if="themeDetail.iscollectioned==false">
              <el-button class="el-icon-plus" size="small" type="warning" round>收藏主题</el-button>
            </div>
            <div @click="collectTheme()" class="btn-sc" v-else>
              <el-button class="el-icon-close" size="small" round>取消收藏</el-button>
            </div>
          </div>
        </div>
        <!--用户信息-->
        <div>
          <ul class="dr_list dr_list2 mt30">
            <li class="clearfix mb15">
              <div class="left headicon bg-img br50" @click="navToIndex(themeDetail.userid)"
                   :style="{backgroundImage:'url('+imgurl+')'}" v-if="!themeDetail.faceimg">
                <div class="bg-img indefiy" :style="{ backgroundImage: 'url(../../static/indefiy.png)' }"
                     v-if="themeDetail.certificationstatus==1">
                </div>
              </div>
              <div class="left headicon bg-img br50" @click="navToIndex(themeDetail.userid)"
                   :style="{backgroundImage:'url('+osshost+ themeDetail.faceimg +userface+')'}" v-if="isThree==false">
                <div class="bg-img indefiy" :style="{ backgroundImage: 'url(../../static/indefiy.png)' }"
                     v-if="themeDetail.certificationstatus==1">
                </div>
              </div>
              <div class="left headicon bg-img br50" @click="navToIndex(themeDetail.userid)"
                   :style="{backgroundImage:'url('+ themeDetail.faceimg +')'}" v-if="isThree==true">
                <div class="bg-img indefiy" :style="{ backgroundImage: 'url(../../static/indefiy.png)' }"
                     v-if="themeDetail.certificationstatus==1">
                </div>
              </div>
              <div class="info mt10">
                <h2 class="text-bold text-17">{{themeDetail.nickname}}</h2>
              </div>
              <a class="right" v-if="themeDetail.relationship == 0 || themeDetail.relationship == 2"
                 @click="attentionUser(themeDetail.userid)">
                <el-button class="el-icon-plus" size="small" type="warning" round>关注</el-button>
              </a>
              <a class="right" v-else @click="unAttentionUser(themeDetail.userid)">
                <el-button class="el-icon-close" size="small" round>取消关注</el-button>
              </a>
            </li>
          </ul>
          <div class="text-df">{{themeDetail.introduction}}</div>
        </div>

        <!-- 这里是 推荐菜谱-->
        <div class="mt50"><span class="text-bold text-25">推荐菜谱</span></div>

        <div v-for="(item, index) in themeDetail.cplist" :key="index" class="tjcp-box">

          <div class="left tjcp-left">
            <div class="food-img" :style="{ backgroundImage: 'url('+osshost + item.cpimg +detailpic+ ')' }"
                 @click="navToDetail(item.recipeid)"></div>
          </div>
          <div class="left tjcp-right">
            <div class="mt10">
              <span class="text-bold text-20 text-cut">{{ item.title }}</span>
            </div>
            <div class="mt10 f-user">
              <el-avatar class="f-user-img" :style="{backgroundImage:'url('+imgurl+')'}" v-if="!item.userfaceimg"></el-avatar>
              <el-avatar class="f-user-img" :style="{backgroundImage:'url('+osshost+ item.userfaceimg +userface+')'}" v-if="item.isThree==false"></el-avatar>
              <el-avatar class="f-user-img" :style="{backgroundImage:'url('+item.userfaceimg+')'}" v-if="item.isThree==true"></el-avatar>
              <span class="text-14">{{ item.nickName }}</span>
            </div>
            <div class="grayColor mt10" @click="like(index)">
              <span class="text-12">被收藏次数: {{ item.cpcollections }}</span>
            </div>
            <div class="mt10 text-12">
              <span class="text-bold">推荐理由:</span>
              <span>{{ item.reason }}</span>
            </div>
          </div>

        </div>
        <!--面包屑导航  -->
        <div class="">
          <p class="fcc text-lips">您当前的位置：<a href="/" title="">首页</a> \ {{themeDetail.title }}</p>
        </div>
        <!-- 这里是 评论-->
        <div class="mt50">
          <h1 id="reply">评论<el-button class="right" type="warning" @click="comment(0, 0)">发表评论</el-button></h1>
          <div class="mt30">

            <el-dialog
              title="评论回复"
              :visible.sync="replyShow"
              width="600px"
              :before-close="handleClose">
              <form class="cu-form-group">
                <textarea :placeholder="input_placeholder" v-model="message"></textarea>
                <div class="clearfix">
                  <button class="cu-btn sm bg-yellow right br5 mt10"
                          @click="postComment()"> 发表评论
                  </button>
                </div>
              </form>
            </el-dialog>
            <div>
              <div>
                <ul class="dr_list">
                  <li class="clearfix mb15" v-for="(item, index) in commentList" :key="index">
                    <div class="left headicon bg-img br50"
                    @click="navToIndex(item.userid)"
                         :style="{backgroundImage:'url('+ imgurl +')'}" v-if="!item.faceimg">
                    </div>
                    <div class="left headicon bg-img br50"
                    @click="navToIndex(item.userid)"
                         :style="{backgroundImage:'url('+ osshost + item.faceimg + userface +')'}" v-if="!item.isThree&&item.faceimg">
                    </div>
                    <div class="left headicon bg-img br50"
                    @click="navToIndex(item.userid)"
                         :style="{backgroundImage:'url('+ item.faceimg + ')'}" v-if="item.isThree&&item.faceimg">
                    </div>
                    <div class="info" style="width:640px">
                      <h4 class="text-bold text-df">{{ item.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="item.userid == themeDetail.userid">作者</span></h4>
                      <p class="grayColor">{{ item.pushtime }} <span class="mainColor"
                                                                     @click="reply(item.commentid,item.userid,item.id,item.nickname)">回复</span></p>

                      <div>
                        <p class="text-df text-height-lg"> {{ item.message }}</p>
                      </div>
                      <ul class="dr_list">
                        <li class="clearfix mb15" v-for="(child, childindex) in item.subreplylist" :key="childindex">
                          <div class="left headicon bg-img br50"
                               :style="{backgroundImage:'url('+imgurl +')'}"
                               @click="navToIndex(child.userid)" v-if="!child.faceimg">
                          </div>
                          <div class="left headicon bg-img br50"
                               :style="{backgroundImage:'url('+ child.faceimg +')'}"
                               @click="navToIndex(child.userid)" v-if="child.isThree&&child.faceimg">
                          </div>
                          <div class="left headicon bg-img br50"
                               :style="{backgroundImage:'url('+ osshost + child.faceimg + userface +')'}"
                               @click="navToIndex(child.userid)" v-if="!child.isThree&&child.faceimg">
                          </div>
                          <div class="info" style="width:440px">
                            <h4 class="text-bold text-df">{{ child.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="child.userid == themeDetail.userid">作者</span></h4>
                            <p class="grayColor">{{ child.pushtime }} <span class="mainColor"
                                                                            @click="reply2(child.id, item.userid, child.userid, child.commentid, child.nickname)">回复</span>
                            </p>
                            <div>
                              <p class="text-14 grayColor" v-if="child.bereplynickname">回复:{{ child.bereplynickname }}</p>
                              <p class="text-df text-height-lg"> {{ child.message }}</p>
                            </div>
                          </div>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
                <div class="text-center mainColor mb15" v-if="loadMore" @click="loadMorep()">点击加载更多</div>
                <div class="text-center grayColor mb15" v-else>没有评论了</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--右边区域-->
      <div id="right" class="mt30">
        <right-view :uid="themeDetail.userid" v-if="themeDetail.userid"></right-view>
      </div>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import RightView from "../components/rightView";
  import Totop from '@/components/totop.vue';
  import FooterBottom from "@/components/footer";

  import {commentList, pushReply, followUser, unfollowUser, collect, unCollect} from '@/api/operate'
  import {getThemeDetail} from '@/api/theme'
  import {Message} from "element-ui";

  export default {
    name: "themeDetail",
    components: {
      RightView,
      headerNav,
      FooterBottom,
      Totop
    },
    data() {
      return {
        loadMore: true,
        osshost: process.env.UPLOAD_URL,
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        replyShow: false,
        reply2Show: false,
        input_placeholder: '来了说两句吧~',
        is_reply: false,
        token: '',
        themeid: '',
        beuserid: 0,
        message: '',
        replyid: 0,
        replyuserid: 0,
        commentid: 0,
        message1: '',
        message2: '',
        page: 1,
        commentList: [],
        themeDetail: '',
        selfInfo: {}
      }
    },
    mounted() {
      this.token = this.$store.state.user.token;
      this.themeid = this.$route.query.id;
      this.getDataInfo(this.themeid);
      this.getCommentList()
    },
    methods: {
      getDataInfo(themeid) {
        getThemeDetail(themeid).then(response => {
          let res = response.data;
          if (res.success) {
            this.themeDetail = res.result;
            let str = new RegExp('http');
            this.isThree = str.test(this.themeDetail.faceimg);
            for(var i=0;i<this.themeDetail.cplist.length;i++){
            	this.themeDetail.cplist[i].isThree = new RegExp('http').test(this.themeDetail.cplist[i].userfaceimg);
            };
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      },
      navToDetail(id) {
        this.$router.push({path: "/cookBook", query: {id}})
      },
      loadMorep(){
          this.page++;
          this.getCommentList()
      },
      getCommentList() {
        commentList(this.themeid, 3, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              let data=res.result;
              for(var i=0;i<data.length;i++){
              	data[i].childCount=5;
              	data[i].isThree = new RegExp('http').test(data[i].faceimg);
              	data[i].childlist=data[i].subreplylist.slice(0,data[i].childCount);
              	for( var j=0;j<data[i].subreplylist.length;j++){
              		data[i].subreplylist[j].isThree = new RegExp('http').test(data[i].subreplylist[j].faceimg);
              	}
              }
              this.commentList = this.commentList.concat(res.result);
            } else {
              this.loadMore = false;
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      comment(userid, commentid) {
      	//评论
      	if (this.token == '') {
      		navigateTo(`../login/pswLogin`);
      	} else {
      		this.replyShow = true; //调起input框
      		this.beuserid = userid;
      		this.commentid = commentid;
      	}
      },
      reply(commentid,userid,id,nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = 0;
        this.replyuserid = 0;
      	this.beuserid = userid;
      	this.commentid = id;
        console.log(this.commentid,this.beuserid);
      },
      reply2(id, topuserid, userid, commentid, nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = id;
      	this.beuserid = topuserid;
      	this.replyuserid = userid;
      	this.commentid = commentid;
      },
      postComment() {
        if (this.token == '') {
          this.$router.push({path: "/login"})
        } else {
          let _this = this;
          let meassage = _this.message;
          let params = {
          	itemid: _this.themeid,
          	itemtype: 3,
          	replyid: _this.replyid,
          	replyuserid: _this.replyuserid,
          	commentid: _this.commentid,
          	beuserid: _this.beuserid,
          	message: _this.message
          };
          pushReply(params).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: res.message,
                duration: 5 * 1000
              })
              document.getElementById("reply").scrollIntoView();
              this.page = 0;
              this.commentList = [];
              this.getCommentList()
              this.is_reply = false; //回复中
              this.replyShow = false; //调起input框
            } else {
              Message({
                message: res.message,
                type: 'error',
                duration: 5 * 1000
              })
              if (res.code == '9999') {
                this.$router.push({path: "/login"})
              }
            }
          }).catch(error => {
            console.log(error)
          })
        }

      },
      collectTheme() {
        if (this.token) {
          if (this.themeDetail.iscollectioned==false) {
            collect(this.themeid, 3).then(response => {
              let res = response.data;
              if (res.success) {
                this.themeDetail.iscollectioned = true;
              } else {
                Message({
                  message: response.message,
                  type: 'error',
                  duration: 5 * 1000
                })
              }
            }).catch(error => {
              console.log(error)
            })
          } else {
            unCollect(this.themeid, 3).then(response => {
              let res = response.data;
              if (res.success) {
                this.themeDetail.iscollectioned = false;
              } else {
                Message({
                  message: response.message,
                  type: 'error',
                  duration: 5 * 1000
                })
              }
            }).catch(error => {
              console.log(error)
            })
          }
        } else {
          this.$router.push({path: "/login"})
        }
      },
      attentionUser(userid) {
        if (this.token) {
          followUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.themeDetail.relationship == 0) {
                this.themeDetail.relationship = 1;
              } else if (this.themeDetail.relationship == 2) {
                this.themeDetail.relationship = 3;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      unAttentionUser(userid) {
        if (this.token) {
          unfollowUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.themeDetail.relationship == 1) {
                this.themeDetail.relationship = 0;
              } else if (this.themeDetail.relationship == 3) {
                this.themeDetail.relationship = 2;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
    }
  }
</script>

<style scoped>
  .mainImg {
    width: 685px;
    height: 456px;
  }

</style>



